import { Component, ViewChild } from '@angular/core';
import { IonicPage, ModalController, ModalOptions, NavController, NavParams, Slides } from 'ionic-angular';

/**
 * Generated class for the CreditShieldPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-credit-shield',
  templateUrl: 'credit-shield.html',
})
export class CreditShieldPage {
  @ViewChild(Slides) slides: Slides;

  isDetailPage: any = this.navParams.get("isDetailPage") || false;
  banner = [
    {
      img: "assets/imgs/banner1.png"
    },
    {
      img: "assets/imgs/banner2.png"
    }
  ]

  listMenu = [
    {
      title: "Rincian Produk",
      handler: "modalProductDetail"
    },
    {
      title: "Untuk Diperhatikan",
      handler: "modalAttention"
    },
    {
      title: "Tentang Perusahaan Asuransi",
      handler: "modalCompanyAbout"
    }
  ]

  listCard = [
    {
      nama: "ADHIE KAMA ESHVARA        ",
      alias: "C59562023113015370183673",
      noHp: "0817293132     ",
      card: "4201920095155956",
      email: "adhieprimavera@gmail.com                "
    },
    {
      nama: "ADHIE KAMA ESHVARA        ",
      alias: "C74122023113015370183672",
      noHp: "0817293132     ",
      card: "4890870051977412",
      email: "adhieprimavera@gmail.com                "
    },
    {
      nama: "ADHIE KAMA ESHVARA        ",
      alias: "C74122023113015370183897",
      noHp: "0817293132     ",
      card: "4890870051978972",
      email: "adhieprimavera@gmail.com                "
    }
  ]

  myModalOptions: ModalOptions = {
    showBackdrop: true,
    enableBackdropDismiss: true,
    enterAnimation: 'modal-md-slide-in',
    leaveAnimation: 'modal-md-slide-out',
  }

  constructor(public navCtrl: NavController, public navParams: NavParams, public modal: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreditShieldPage');
    if (!this.isDetailPage) {
      // this.slides.lockSwipeToPrev(true);
    } else {
      let menu = {
        title: "Manfaat Asuransi",
        handler: "modalCompanyAbout"
      }

      this.listMenu.splice(0, 0, menu);
    }
  }

  slideChanged() {
    let currentIndex = this.slides.getActiveIndex();
    console.log('Current index is', currentIndex);

    // if (currentIndex == 0) {
    //   this.slides.lockSwipeToPrev(true);
    //   this.slides.lockSwipeToNext(false);
    // } else if (currentIndex == 1) {
    //   this.slides.lockSwipeToPrev(false);
    //   this.slides.lockSwipeToNext(true);
    // }
  }

  handlerMenu(item) {
    if (item == 'modalAttention') {
      this.myModalOptions.cssClass = 'modal-bg'
      this.toModalAttention();
    }
    if (item == 'modalProductDetail') {
      this.myModalOptions.cssClass = 'modal-bg'
      this.toModalProductDetail();
    }
    if (item == 'modalCompanyAbout') {
      this.myModalOptions.cssClass = 'modal-company-bg'
      this.toModalCompanyAbout();
    }
  }

  toModalManfaat() {
    const modalManfaat = this.modal.create("CreditShieldBottomModalPage", { isModalManfaat: true }, this.myModalOptions);
    modalManfaat.present();
  }

  toModalAttention() {
    const modalManfaat = this.modal.create("CreditShieldBottomModalPage", { isModalAttention: true }, this.myModalOptions);
    modalManfaat.present();
  }

  toModalProductDetail() {
    const modalManfaat = this.modal.create("CreditShieldBottomModalPage", { isModalProductDetail: true }, this.myModalOptions);
    modalManfaat.present();
  }

  toModalCompanyAbout() {
    const modalManfaat = this.modal.create("CreditShieldBottomModalPage", { isModalCompanyAbout: true }, this.myModalOptions);
    modalManfaat.present();
  }

  toPanduanKlaim() {
    this.navCtrl.push("CreditShieldGuidePage");
  }

  toApply() {
    this.listCard.forEach((element) => {
      element["model"] = false;
    })

    console.log('listCard', this.listCard);
    this.navCtrl.push("CreditShieldApplyPage");
  }

  goBack() {
    this.navCtrl.pop();
  }

}



